import axios from 'axios' ;

export default{
    /**登录请求 */
    localLogin(data){ 
        return axios.post('/api/login',data)
    },
    /**添加分类 */
    addClassify(params){
        return axios.post('/api/classify/create',params)
    },
    /**获取分类列表 */
    getClassify(){
        return axios.get('/api/classify/lists')
    },
    /**删除分类 */
    removeClassifyList(params){
        return axios.post('/api/classify/remove',params)
    },
    /**根据分类查找文章 */
    getArticlesByClassify(params){
        return axios.post('/api/article/articleClassify',params)
    },
    /**编辑分类 */
    editClassfy(params){
        return axios.post('/api/classify/edit',params)
    },
    /**创建文章 */
    createArticle(params){
        return axios.post('/api/article/create',params)
    },
    /**获取所有文章列表 带分页*/
    getArticleList(data){
        return axios.post('/api/article/lists',data)
    },
    /**获取所有文章 不带分页*/
    getAllArticle(data){
        return axios.post('/api/article/Alllists',data)
    },
    /**删除某篇文章 */
    removeOneArticle(params){
        return axios.post('/api/article/remove',params)
    },
    /**得到某便文章 */
    getOneArticle(params){
        return axios.post('/api/article/onePage',params)
    },
    /**更新文章 */
    editArticle(params){
        return axios.post('/api/article/edit',params)
    },
    /**修改关于内容 */
    editAbout(params){
        return axios.post('/api/info/edit',params)
    }
}


