import Vue from 'vue'
import Router from 'vue-router'
const Home = r =>require.ensure([],()=>r(require('@/components/fronted/Home')),'front') 
const Font = r =>require.ensure([],()=>r(require('@/components/fronted/Font')),'front') 
const Article = r =>require.ensure([],()=>r(require('@/components/fronted/Article')),'front') 
const Tag = r =>require.ensure([],()=>r(require('@/components/fronted/Tags')),'front') 
const About = r =>require.ensure([],()=>r(require('@/components/fronted/About')),'front')

const Login = r =>require.ensure([],()=>r(require('@/components/backEnd/Login')),'back') 
const Admin = r =>require.ensure([],()=>r(require('@/components/backEnd/Admin')),'back') 
const ArticleList = r =>require.ensure([],()=>r(require('@/components/backEnd/ArticleList')),'back-article') 
const ArticleCreate = r =>require.ensure([],()=>r(require('@/components/backEnd/ArticleCreate')),'back-article-create') 
const ArticleEdit = r =>require.ensure([],()=>r(require('@/components/backEnd/ArticleEdit')),'back-article-edit') 
const ClassList = r =>require.ensure([],()=>r(require('@/components/backEnd/ClassList')),'back-class') 
const ClassCreate = r =>require.ensure([],()=>r(require('@/components/backEnd/ClassCreate')),'back-class') 
const Info = r =>require.ensure([],()=>r(require('@/components/backEnd/Info')),'back-info') 
const cIndex = r =>require.ensure([],()=>r(require('@/components/backEnd/index')),'back') 

import store from '../store'
Vue.use(Router)

var router = new Router({
  mode:'history',
  base: __dirname, 
  routes: [
    {
      path: '/',
      component: Font, //文章页面
      hidden:true,
      redirect: { name: 'home' },
      meta:{auth:false},
      children:[
        {path:'home',name:'home',component:Home,meta:{auth:false}},
        {path:'about',name:'about',component:About,meta:{auth:false}},
        {path:'tags',name:'tags',component:Tag,meta:{auth:false}},
        {path:'article/:id',name:'about',component:Article,meta:{auth:false}},
      ]
    },
    {
      path:'/login',
      hidden:true,
      name:'login',
      component:Login,
      meta:{auth:true}
    },
    {
      path:'/admin',
      name:'adminPanel',
      iconCls: 'el-icon-message',
      component: Admin, //文章页面
      name:'管理页面',
      meta:{auth:true},
      redirect: {name:'文章管理'},
      children:[
        {
        name:'文章管理',
        meta:{auth:true},
        path:'article',
        component:cIndex,
        redirect: {name:'文章列表'},
        children:[
          {path:'articleList',name:'文章列表',component:ArticleList,meta:{auth:true}},
          {path:'articleCreate',name:'文章创建',component:ArticleCreate,meta:{auth:true}},
          {path:'articleEdit/:postId',name:'文章编辑',component:ArticleEdit,meta:{auth:true},hidden:true},
        ]
        },
        {
        name:'分类管理',
        path:'categories',
        meta:{auth:true},
        redirect:{name:'分类管理'},
        component:cIndex,
        children:[
          {path:'ClassList',name:'分类管理',component:ClassList,meta:{auth:true}},
        ]
        },
        {
          name:'其他',
          path:'info',
          meta:{auth:true},
          redirect:{name:'其他'},
          component:cIndex,
          children:[
            {path:'info',name:'关于',component:Info,meta:{auth:true}},
          ]
        },
      ]
    }
  ]
})
router.beforeEach(({meta,path},from,next)=>{
  // NProgress.start();
  let {auth=true}=meta
  let isLogin = Boolean(store.state.token)
  if(auth&&!isLogin&&path!=='/login'){
    return next({path:'/login'})
  }
  // 如果登录了以后再访问login则路由到Home
  if(isLogin&&(path=='/login')){
    return next({path:'/admin'});
  }
  // 未登录的情况下访问reg则直接路由
  next();
})




export default router


